@import url('https://fonts.googleapis.com/css2?family=Red+Rose:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 7rem;
  margin-top: 5rem;
}
.app {
  min-height: 100vh;
  height: 100%;
  display: flex;
  justify-content: center;
  margin-top: 5rem;
  gap: 3rem;
}

.container {
  height: 50rem;
  max-width: 70rem;
  width: 100%;
  border: 3px solid var(--color3);
  border-radius: 0.4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
}

.images-container {
  margin-top: 20px;
  padding: 10px;
}

.images-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 20px;
}

.image {
  width: 150px;
  height: 100px;
  object-fit: cover; /* Ensures images are nicely cropped to fit their containers */
  border-radius: 8px; /* Optional: Adds rounded corners to images */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: Adds a subtle shadow to images */
}